@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer{
    background-color: v.$primario;
    padding-top: 5rem;
    margin-top: 5rem;

    &__grid{
        @include m.tablet{
            @include m.grid(3, 2rem);

        }
    }
    &__heading{
        color: v.$blanco;
        text-transform: uppercase;
        font-size: 2rem;
        text-align: center;
    }
    &__texto{
        color: v.$blanco;
        text-align: center;
        svg{
            width: 5rem;
        }
    }
    &__navegacion{
        display: flex;
        flex-direction: column;
        text-align: center;

    }
    &__link{
        color: v.$blanco;
        font-size: 1.8rem;
        margin-bottom: 1rem;

        &:last-of-type{
            margin-bottom: 0;
        }
    }
    &__copyright{
        background-color: darken(v.$primario, 10%);
        text-align: center;
        padding: 1rem;
        margin: 5rem 0 0 0;
        color: v.$blanco;
        .silakweb{
            margin-left: 2rem;
            font-size: 1.2rem;
            color: #989898;
            font-style: oblique;
        }
    }
}