@use 'base/variables' as v;
@use 'base/mixins' as m;

//PÃ¡gina Servicios
.servicios{
    height: auto;
    padding: 2rem 0;
    background-image: linear-gradient(to right, transparentize(v.$gris, .1) 0%, transparentize(v.$primario, .1) 100%), url(../img/propiedad_4.jpg);
    background-size: cover;
    background-position: center;
}

.productos{
    @include m.desktop{
        width: 70%;
    }
    &__titulo{
        margin-top: 2rem;
        @include m.heading();
        font-family: v.$fuentePrincipal;
    }

    &__flex{
        margin: 5rem 0;
        @include m.telefono{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            gap: 2rem;
        }
        @include m.desktop{
            flex-direction: row;
 
        }
    }

    &__imagen{
        flex: 1;
        @include m.desktop{
            img{
                max-width: 400px;
                max-height: 400px;
            }
        }

        img{
            max-width: auto;
            max-height: auto;
            object-fit: cover;
        }
    }
    &__descripcion{
        padding: 0 2rem;
        text-align: center;
        flex: 2;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    &__flex:nth-child(even){
        @include m.desktop{
            flex-direction: row-reverse;
        }
        background-color: v.$primario;
        color: white;
        .productos__descripcion p{
            color: white;
        }
    }
}