@use 'base/variables' as v;
@use 'base/mixins' as m;

#whatsapp {
    z-index: 5;
    position: fixed;
    bottom: 20px;
    right:20px;
 }
 svg {
    color: white;
    width: 80px;
    filter: drop-shadow(0 1px 4px rgba(0,0,0,.4));
 }
 circle {
    fill: #25d366;
 }
 path {
    fill: #fff;
 }
 #whatsapp:hover::before{
    background-color: white;
    color: black;
    font-weight: 400;
    border-radius: .5rem;
    position: absolute;
    border: 2px solid #25d366;
    content: 'ðŸ‘‹ Hola soy Liliana, Â¿En quÃ© puedo ayudarte?';
    width: 20rem;
    height: 8rem;
    top: 0;
    right: 110px;
    text-align: center;
    padding-top: 20px;
 }
 #whatsapp:hover::after{
    content: '';
    clip-path: polygon(0 0, 0% 100%, 100% 50%);
    background-color: #25d366;
    position: absolute;
    width: 3rem;
    height: 3rem;
    right: 80px;
    top: 25px;
 }

.header{
    background-image: linear-gradient(to right, transparentize(v.$gris, .1) 0%, transparentize(v.$primario, .1) 100%), url(../img/header_bg.jpg);
    background-size: cover;
    background-position: center;
    padding: 3rem 0;
    position: relative;
    @include m.tablet{
        padding: 3rem 0 20rem 0;
    }

    &__barra{
        display: flex;
        flex-direction: column;
        align-items: center;
        @include m.tablet{
            flex-direction: row;
            justify-content: space-between;
        }
    }
    &__logo{
        width: 25rem;
    }
    &__contenido {
        @include m.tablet{
            margin-top: 10rem;
            max-width: 50rem;
        }
    }
    &__heading{
        color: v.$primario;
        text-align: center;
        animation: move 2s ease-in 0s 1 normal backwards;
        @include m.tablet{
            text-align: left;
        }
    }
    &__texto{
        color: v.$primario;
        display: none;
        animation: move 2s ease-in 0s 1 normal backwards;
        @include m.tablet{
            display: block;
        }
    }
    @keyframes move {
	0% {
		opacity: 0;
		transform: translateX(-50px);
	}

	100% {
		opacity: 1;
		transform: translateX(0);
	}
}
    &__imagen{
        display: none;
        @include m.tablet{
            transform: scaleX(-1);
            display: block;
            position: absolute;
            right: 0;
            bottom: -5rem;
            max-width: 35rem;
            border-top-right-radius: 2rem;
            border-bottom-right-radius: 5rem;

        }
        @include m.desktopXL{
            max-width: 60rem;
        }
    }

}
.navegacion{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 5rem;
    @include m.tablet{
        flex-direction: row;
        gap: 2rem;
        margin-top: 0;
    }
    &__link{
        color: v.$blanco;
        font-size: 1.8rem;
        margin-bottom: 2rem;
        border-bottom: 2px solid transparent;
        padding-bottom: 1px;
        transform: border;
        &:hover{
            transition: .5s ease-in;
            border-bottom: 2px solid v.$blanco;
        }
        &:last-of-type{
            margin-bottom: 0;
        }
        @include m.tablet{
            margin-bottom: 0;
        }
    }
}

