@use 'base/variables' as v;
@use 'base/mixins' as m;

.camaras{
    &__titulo{
        margin-bottom: 2rem;
        @include m.heading();
    }
    margin-top: 5rem;
    &__grid{
        @include m.telefono{
            display: grid;
            grid-template-columns: repeat(4, 250px);
            overflow-x: scroll;
            overscroll-behavior-x: contain;
            scroll-snap-type: x proximity;
        }

        @include m.tablet{
            overflow-y: hidden;
            display: flex;
            flex-wrap: wrap;
            gap: 2rem;
            justify-content: center;
            align-content: center;
            flex-direction: row;
        }

    }
    &__imagen{
        position: relative;
        width: 25rem;
        height: 25rem;
        img{
            width: 25rem;
            height: 25rem;
            object-fit: cover;
        }
    }

    &__imagen:nth-child(1):hover::after{
        cursor: pointer;
        animation: efecto 1s ease 0s 1 normal forwards;
        background-color: v.$primario;
        position: absolute;
        top: 0;
        left: 0;
        width: 25rem;
        height: 25rem;
        display: grid;
        place-items: center;
        content:'CÃ¡maras de Seguridad';
        font-weight: 900;
        font-family: v.$fuentePrincipal;
        color: white;
    }
    &__imagen:nth-child(2):hover::after{
        cursor: pointer;
        animation: efecto 1s ease 0s 1 normal forwards;
        background-color: v.$primario;
        position: absolute;
        top: 0;
        left: 0;
        width: 25rem;
        height: 25rem;
        display: grid;
        place-items: center;
        content:'Sistemas de Alarma';
        font-weight: 900;
        font-family: v.$fuentePrincipal;
        color: white;
    }
    &__imagen:nth-child(3):hover::after{
        cursor: pointer;
        animation: efecto 2s ease 0s 1 normal forwards;
        background-color: v.$primario;
        position: absolute;
        top: 0;
        left: 0;
        width: 25rem;
        height: 25rem;
        display: grid;
        place-items: center;
        content:'Sistemas de Monitoreo';
        font-weight: 900;
        font-family: v.$fuentePrincipal;
        color: white;
    }
    &__imagen:nth-child(4):hover::after{
        cursor: pointer;
        animation: efecto 1s ease 0s 1 normal forwards;
        background-color: v.$primario;
        position: absolute;
        top: 0;
        left: 0;
        width: 25rem;
        height: 25rem;
        display: grid;
        place-items: center;
        content:'Mantenimiento - Post Venta';
        font-weight: 900;
        font-family: v.$fuentePrincipal;
        color: white;
    }
    @keyframes efecto {
        0% {
            opacity: 0;
            transform: rotateX(100deg);
            transform-origin: bottom;
        }
    
        100% {
            opacity: 1;
            transform: rotateX(0);
            transform-origin: bottom;
        }
    }

}