@use 'base/variables' as v;
@use 'base/mixins' as m;

// Animation
@keyframes scroll {
	0% { transform: translateX(0); }
	100% { transform: translateX(calc(-250px * 7))}
}
// Styling
.slider {
    &__titulo{
        margin-top: 2rem;
        @include m.heading();
    }
	background: white;
	box-shadow: 0 10px 20px -5px rgba(0, 0, 0, .125);
	height: 250px;
	margin: 5rem auto;
	overflow:hidden;
	position: relative;
	
	&::before,
	&::after {
		@include m.white-gradient;
		content: "";
		height: 250px;
		position: absolute;
		width: 250px;
		z-index: 2;
	}
	
	&::after {
		right: 0;
		top: 0;
		transform: rotateZ(180deg);
	}

	&::before {
		left: 0;
		top: 0;
	}
	
	.slide-track {
		animation: scroll v.$animationSpeed linear infinite;
		display: flex;
		width: calc(250px * 11);
	}
	
	.slide {
		height: 250px;
		width: 250px;
        img{
            width: 250px;
            height: 250px;
            object-fit: contain;
        }
	}
}