@use 'base/variables' as v;
@use 'base/mixins' as m;

.nosotros{
    height: auto;
    padding: 2rem 0;
    background-image: linear-gradient(to right, transparentize(v.$gris, .1) 0%, transparentize(v.$primario, .1) 100%), url(../img/propiedad_4.jpg);
    background-size: cover;
    background-position: center;
}

.contenedor{
    &__flex{
        width: 80%;
        margin: 5rem auto;
        @include m.tablet{
            display: flex;
            flex-direction: row-reverse;
            gap: 2rem;
            margin-bottom: 2rem;
            min-height: 20rem;
            justify-content: center;
            align-items: center;
        }

    }
    &__flex:nth-child(2){
        flex-direction: row;
    }
    &__texto{
        flex: 2;
    }
    &__titulo{
        @include m.heading;

    }
    &__imagen{
        flex: 1;

        img{
            width: 100%;
            object-fit: cover;
        }
        
    }
}